import React from 'react';
import { Link } from 'react-router-dom';

const Featuredcard = (props) => {
    return(
        <Link to={`${ props.projectURI }`}>
            <div className={`featuredcard ${ props.styleClass }`} style={{backgroundImage:`url(${ props.projectImg })`}}>

            </div>
        </Link>
    )
}

export default Featuredcard;