import React from 'react';

function Resourcecard(props){
    return(
        <div className="resource-card">
            <div className="label-dim small mt-small">
                {props.resourceTag}
            </div>
            <a href={props.resourceURI}>
                <div className="resourcecard mt-tiny" style={{backgroundImage:`url(${ props.resourceImage })`}}>
                </div>
                <div>
                    <div className="card-title__container"><h4 className="card-title">{props.resourceTitle}</h4><span class="price">Free</span></div>
                    <div className="clearfix"></div>
                    <div className="excerpt">{props.resourceExperpt}</div>
                </div>
            </a>
        </div>
    )
}
export default Resourcecard;