import React, { useState } from 'react';
import messageHappyIcon from '../images/message-happy.svg';
import messageIcon from '../images/message.svg';
import { Link } from 'react-router-dom';

function Mainbutton(props){
    const [mainbuttonimg, setMainbuttonimg] = useState(messageIcon)

    return(
        <div className="contact-cta-container">
            <a
                href="#home-contact"
                >
                <button
                    className="contact-cta"
                    onMouseOver={e => setMainbuttonimg(messageHappyIcon)}
                    onMouseOut={e => setMainbuttonimg(messageIcon)}
                    onClick={e => props.showModal()}
                    >
                    <img
                        className="icon" src={mainbuttonimg} alt="icon"
                        />
                    Say Hi
                </button>
            </a>
        </div>
    )
}

export default Mainbutton;