import React, { useState } from 'react';
import { Route } from 'react-router-dom';
import About from './About';
import Navigation from './Navigation';
import Topbar from './Topbar';
import Blog from './Blog';
import Footer from './Footer';
import Contact from './Contact';
import Content from './Content';
import Thankyou from './Thankyou';
import ResourceSection from './ResourceSection';
import SingleBlog from './SingleBlog';
import Bottom from './Home/Bottom';
import CreateSomethingEveryday from '../posts/Create-something-everyday';
import Neptoons from '../resources/Neptoons';

const Home = props => {
    const [isModal, setIsModal] = useState(false)

    const showModal = () => {
        setIsModal(!isModal)
    }

    return(
        <div>
            <div className="home position-fixed">
                <div className="container">
                    <div className="row no-gutters">
                        <div className="col">
                            <header className="App-header">
                                <Topbar/>
                            </header>
                        </div>
                    </div>

                    <div className="row no-gutters">
                        <div className="col-2 d-none d-md-block">
                            <Navigation navColor={props.navColor}/>
                        </div>
                        {/* Main Nav */}
                        <Route path="/" exact render={props => <Content {...props} showModal={showModal}/>}/>
                        <Route path="/contact/" render={props => <Contact {...props} isModal={isModal}/>}/>
                        <Route path="/about/" component={About}/>
                        <Route path="/someuri/" component={SingleBlog}/>
                        <Route path="/thankyou/" component={Thankyou}/>

                        {/* Posts */}
                        <Route path="/posts/create-something-everyday/" component={CreateSomethingEveryday}/>

                        {/* Posts */}
                        <Route path="/resources/neptoons" component={Neptoons}/>

                        {/* Products */}
                        <div className="col-2 d-none d-md-block">
                        </div>
                    </div>
                </div>
                <Route path="/" exact component={Bottom}/>
                <Footer/>
            </div>
        </div>
    )
}
export default Home;