import React from 'react';

function Featuredblog(props){
    return(
        <div className="blog-card">
            <div className="label-dim small mt-small">
                {props.blogTag}
            </div>
            <a href={props.blogURI}>
                <div className="featuredblog mt-small" style={{backgroundImage:`url(${ props.blogImage })`}}>
                </div>
                <div>
                <h4 className="card-title">{props.blogTitle}</h4>
                <div className="excerpt">{props.blogExperpt}</div>
                </div>
            </a>
        </div>
    )
}
export default Featuredblog;