import React, { useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';

function Contact(props){
    const [buttonText, setbuttonText] = useState('Send')
    const [nameText, setnameText] = useState()
    const [emailText, setemailText] = useState()
    const [messageText, setmessageText] = useState()
    const [sent, setSent] = useState(false)

    const formSubmit = async (e) => {
        e.preventDefault()

        setbuttonText('Sending')

        let data = {
            name: nameText,
            email: emailText,
            message: messageText
        }

        try {
            await axios.post('https://portfolio-akriti.now.sh/api/v1', data)
            setbuttonText('Sent')
            setSent(true)
        } catch(err) {
            console.log(err)
        }

    }

    return(
    <section className={"contact section"}>
        {/* {(props.isModal) ? <Headshot/> : ''} */}
        <div className="container" id="home-contact">
            <div className="row">
                <div className="col-3"></div>
                <div className="col">
                    <h3 className="section-title">
                    Get in Touch
                    </h3>
                    <div className="contact-subtext">Write me a message below or drop me an email at <a href="mailto:madebyakriti@gmail.com"><strong>madebyakriti@gmail.com</strong></a></div>
                    <form className="contact-form" action="" onSubmit={e => formSubmit(e)}>
                        <input
                            onChange={e => setnameText(e.target.value)}
                            id="contactname" 
                            name="contactname"
                            type="text"
                            placeholder="Your Name"
                            required/>
                        <input
                            onChange={e => setemailText(e.target.value)}
                            id="contactemail"
                            type="email"
                            placeholder="Your Email Address"
                            required/>
                        <textarea 
                            onChange={e => setmessageText(e.target.value)}
                            id="contactmessage"
                            type="text"
                            placeholder="Your Message.."/>
                        <button type="submit" className="contact-cta">{buttonText}</button>
                    </form>
                </div>
                <div className="col-3"></div>
            </div>
        </div>
    </section>
    )
}

export default Contact;