import React from 'react';
import Resourcecard from './Resourcecard';
import Designresources from '../designresources.json';

function Resource() {
    return(
        <div className="row">
            {Designresources.map( (post, index) =>
                <div className="col-xs-12 col-sm-6 col-md-4">
                    <Resourcecard resourceImage={post.resourceImage} resourceURI={post.resourceURI} resourceTitle={post.resourceTitle} resourceExperpt={post.resourceExperpt} resourceTag={post.resourceTag}/>
                </div>
            )}
        </div>
    )
}
export default Resource;