import React, { Component } from 'react';
import Home from './components/Home';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import ReactGA from 'react-ga';
import { createBrowserHistory } from 'history';
import { Router } from "react-router-dom";

ReactGA.initialize('UA-147207731-1');

const history = createBrowserHistory()
history.listen((location) => {
  ReactGA.set({ page: location.pathname });
  ReactGA.pageview(location.pathname);
});


class App extends Component {
  state = {
    color: 'black'
  }

  listenScrollEvent = e => {
    let element = document.getElementById('scroller');
    if (element) {
      let scrollPos = element.getBoundingClientRect().top;
      if ( scrollPos < 195 && scrollPos > -780) {
        this.setState({color: 'white'})
      } else {
        this.setState({color: 'black'})
      }
    }
  }

  render() {
    return (
      <Router history={history}>
      <div className="App" onScroll={e => this.listenScrollEvent(e)}>
          <Home navColor={this.state.color}/>
      </div>
      </Router>
    );
  }
}

export default App;
