import React from 'react';
import Featuredcard from './Featuredcard';
import Masonry from 'react-masonry-css';
import featuredAssets from '../featuredAssets.json'

function Featured() {
    return(
        <Masonry
            breakpointCols={3}
            className="my-masonry-grid"
            columnClassName="my-masonry-grid_column"
            >
            {featuredAssets.map( (item, index) =>
                <Featuredcard
                key={item.projectImage}
                projectImg={item.projectImage}
                projectURI={item.projectURI}
                styleClass={index === 1 ? 'smallcard' : ''}
                />
            )}
        </Masonry>
    )
}

export default Featured;